@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Nova+Round&display=swap");
h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
}

h1 {
  font-size: 36px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 24px;
}

h4 {
  font-size: 20px;
}

h5 {
  font-size: 16px;
}

h6 {
  font-size: 14px;
}

.nav {
  padding: 25px;
  font-size: 20px;
}
.nav .logo_container {
  width: fit-content;
  margin-left: 5%;
  transition: all 0.3s;
  position: relative;
  display: inline-block;
}
.nav a {
  display: inline-block;
  text-underline-offset: 5px;
}
.nav .logo_link {
  color: #222222;
}
.nav .logo_link:hover {
  background-color: rgba(255, 255, 255, 0);
}
.nav .logo_container::before {
  content: "";
  position: absolute;
  width: 100px;
  height: 10px;
  background-color: #b0b0b0;
  border-radius: 50%;
  transform: translate(-5%, 100%) scale(0);
  transition: transform 0.5s ease;
  z-index: -1;
}
.nav .logo_container:hover::before {
  transform: translate(-5%, 100%) scale(1);
}
.nav .logo {
  display: block;
  position: relative;
  transition: transform 0.5s ease;
  z-index: 1;
}
.nav .logo:hover {
  transform: translateY(-5px);
}
.nav .nav_list_item_active {
  text-decoration: underline;
}

.container {
  display: grid;
  grid-template-columns: 2fr 1fr;
  flex-direction: row;
}
.container ul {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.theme_btn {
  order: 9;
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 51px;
  height: 27px;
  padding: 5px;
  border-radius: 50px;
}
.theme_btn img {
  width: 40%;
  height: auto;
}

.theme_btn::before {
  content: "";
  position: absolute;
  top: 1px;
  left: 1px;
  display: block;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #fff;
  transition: left 0.2s ease-in;
}

.theme_btn-active::before {
  left: 24px;
}

.dark_theme_btn_icon {
  position: relative;
  z-index: 9;
}

/* Адаптивная верстка */
/* Для маленьких экранов (мобильные устройства, до 576px) */
@media (max-width: 576px) {
  .nav {
    padding: 15px;
    font-size: 16px;
  }
  .nav .logo_container {
    margin-left: 0;
    width: 100%;
    text-align: center;
  }
  .nav ul {
    flex-direction: column;
    align-items: center;
    padding: 0;
  }
  .nav a {
    padding: 10px;
    display: block;
    text-align: center;
  }
  .container {
    grid-template-columns: 1fr;
  }
  .container ul {
    justify-content: center;
    flex-direction: column;
  }
  .theme_btn {
    width: 45px;
    height: 25px;
  }
  .theme_btn::before {
    width: 22px;
    height: 22px;
  }
}
/* Для планшетов и средних экранов (от 576px до 768px) */
@media (min-width: 577px) and (max-width: 768px) {
  .nav {
    padding: 20px;
    font-size: 18px;
  }
  .nav ul {
    flex-direction: row;
    justify-content: space-between;
  }
  .nav .logo_container {
    margin-left: 2%;
    width: fit-content;
  }
  .nav a {
    padding: 8px;
  }
  .container {
    grid-template-columns: 2fr 1fr;
  }
  .container ul {
    justify-content: space-around;
  }
  .theme_btn {
    width: 48px;
    height: 26px;
  }
  .theme_btn::before {
    width: 23px;
    height: 23px;
  }
}
/* Для больших экранов (от 768px до 992px) */
@media (min-width: 769px) and (max-width: 992px) {
  .nav {
    padding: 25px;
    font-size: 20px;
  }
  .nav ul {
    justify-content: space-between;
  }
  .nav a {
    padding: 7px;
  }
  .container {
    grid-template-columns: 2fr 1fr;
  }
  .theme_btn {
    width: 50px;
    height: 27px;
  }
  .theme_btn::before {
    width: 24px;
    height: 24px;
  }
}
/* Для очень больших экранов (от 992px и выше) */
@media (min-width: 993px) {
  .nav {
    padding: 25px;
    font-size: 20px;
  }
  .nav ul {
    justify-content: space-around;
  }
  .nav a {
    padding: 7px;
  }
  .container {
    grid-template-columns: 2fr 1fr;
  }
  .theme_btn {
    width: 51px;
    height: 27px;
  }
  .theme_btn::before {
    width: 24px;
    height: 24px;
  }
}
.main_header {
  width: 100%;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main_header .intro {
  flex-wrap: wrap;
  width: 50%;
  font-size: 30px;
}

.about {
  padding: 30px;
  display: grid;
  grid-template-columns: 1.7fr 1.3fr;
}
.about .avatar img {
  width: 512px;
  height: auto;
  border-radius: 10px;
}
.about .skills {
  margin-top: 10px;
}
.about .skills ul {
  margin-top: 3%;
  margin-left: 7%;
  font-size: 20px;
}
.about .skills ul li span {
  font-family: "Courier New", sans-serif;
}
.about .skills ul li img {
  width: 25px;
  height: auto;
}
.about .skills .skills_list,
.about .skills .skills_list li {
  list-style: disc;
}
.about .skills .about_me_list {
  margin-bottom: 5%;
}
.about .skills .about_me_list,
.about .skills .about_me_list li {
  list-style-type: "-";
}

@media (max-width: 576px) {
  .about {
    display: flex;
    flex-direction: column;
  }
}
@media (min-width: 577px) and (max-width: 768px) {
  .about {
    display: flex;
    flex-direction: column;
  }
}
footer {
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 20vh;
  margin-top: auto;
}
footer .contactLinks {
  display: flex;
  align-self: center;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 5px;
  width: 35%;
}
footer img {
  width: 32px;
  height: auto;
}

@media (max-width: 576px) {
  img {
    width: 64px;
    height: auto;
  }
}
.projects_main {
  display: flex;
  justify-content: center;
  align-items: center;
}

body {
  font-family: "Nova Round", sans-serif;
  font-weight: 400;
  font-style: normal;
  min-height: 100vh;
}

body.dark-theme {
  background-color: #121212;
  color: #E0E0E0;
}

#root {
  min-height: 100vh;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.light-theme h1, .light-theme h2, .light-theme h3, .light-theme h4, .light-theme h5, .light-theme h6 {
  color: #8A2BE2;
}
.light-theme .logo {
  color: #222222;
}
.light-theme .secondary_text {
  color: #b0b0b0;
  font-weight: 600;
}
.light-theme .logo_container::before {
  background-color: #b0b0b0;
}
.light-theme a {
  color: #8A2BE2;
  padding: 7px;
  border-radius: 10px;
}
.light-theme a:hover {
  background-color: #E6E6FA;
}
.light-theme button {
  background-color: #E6E6FA;
  color: #222222;
}

.dark-theme h1, .dark-theme h2, .dark-theme h3, .dark-theme h4, .dark-theme h5, .dark-theme h6 {
  color: #6A0DAD;
}
.dark-theme .logo {
  color: #E0E0E0;
}
.dark-theme .secondary_text {
  color: #c9c9c9;
  font-weight: 600;
}
.dark-theme .logo_container::before {
  background-color: #c9c9c9;
}
.dark-theme a {
  color: #6A0DAD;
  padding: 7px;
  border-radius: 10px;
}
.dark-theme a:hover {
  background-color: #D8BFD8;
}
.dark-theme button {
  background-color: #D8BFD8;
  color: #E0E0E0;
}

body, a, button {
  transition: all 0.3s;
}

/* Для небольших экранов (мобильные устройства, до 576px) */
@media (max-width: 576px) {
  body {
    font-size: 14px;
  }
  h1 {
    font-size: 24px;
  }
  h2 {
    font-size: 20px;
  }
  .logo_container {
    width: 100px;
  }
  .navbar {
    flex-direction: column;
    align-items: center;
  }
  .footer {
    font-size: 12px;
  }
}
/* Для планшетов и средних экранов (от 576px до 768px) */
@media (min-width: 577px) and (max-width: 768px) {
  body {
    font-size: 16px;
  }
  h1 {
    font-size: 28px;
  }
  h2 {
    font-size: 24px;
  }
  .logo_container {
    width: 120px;
  }
  .navbar {
    justify-content: space-between;
  }
}
/* Для больших экранов (от 768px до 992px) */
@media (min-width: 769px) and (max-width: 992px) {
  body {
    font-size: 18px;
  }
  h1 {
    font-size: 32px;
  }
  h2 {
    font-size: 28px;
  }
  .logo_container {
    width: 140px;
  }
  .navbar {
    justify-content: space-around;
  }
  .footer {
    font-size: 14px;
  }
}
/* Для очень больших экранов (от 992px и выше) */
@media (min-width: 993px) {
  body {
    font-size: 20px;
  }
  h1 {
    font-size: 36px;
  }
  h2 {
    font-size: 32px;
  }
  .logo_container {
    width: 160px;
  }
  .navbar {
    justify-content: flex-start;
  }
}

