footer{
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 20vh;
  margin-top: auto;
  .contactLinks {
    display: flex;
    align-self: center;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 5px;
    width: 35%;
  }
  img{
    width: 32px;
    height: auto;
  }
}

