@import url('https://fonts.googleapis.com/css2?family=Nova+Round&display=swap');

@import "variables";
@import "headlines";
@import "navbarStyles";
@import "headerStyles";
@import "aboutStyles";
@import "footerStyles";
@import "projectsPage";

body {
  font-family: "Nova Round", sans-serif;
  font-weight: 400;
  font-style: normal;
  min-height: 100vh;
}

body.dark-theme{
  background-color: $background-color-dark;
  color: $text-color-dark;
}

#root {
  min-height: 100vh;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

@mixin theme($primary-color, $background-color, $text-color, $accent-color, $secondary-color) {
  h1, h2, h3, h4, h5, h6 {
    color: $primary-color;
  }
  .logo{
    color: $text-color;
  }
  .secondary_text {
    color: $secondary-color;
    font-weight: 600;
  }
  .logo_container::before {
    background-color: $secondary-color;
  }
  a {
    color: $primary-color;
    padding: 7px;
    border-radius: 10px;
  }
  a:hover {
    background-color: $accent-color;
  }

  button {
    background-color: $accent-color;
    color: $text-color;
  }
}

.light-theme {
  @include theme($primary-color-light, $background-color-light, $text-color-light, $accent-color-light, $secondary-color-light);
}

.dark-theme {
  @include theme($primary-color-dark, $background-color-dark, $text-color-dark, $accent-color-dark, $secondary-color-dark);
}

body, a, button {
  transition: all 0.3s;
}

/* Для небольших экранов (мобильные устройства, до 576px) */
@media (max-width: 576px) {
  body {
    font-size: 14px;
  }

  h1 {
    font-size: 24px;
  }

  h2 {
    font-size: 20px;
  }

  .logo_container {
    width: 100px;
  }

  .navbar {
    flex-direction: column;
    align-items: center;
  }

  .footer {
    font-size: 12px;
  }
}

/* Для планшетов и средних экранов (от 576px до 768px) */
@media (min-width: 577px) and (max-width: 768px) {
  body {
    font-size: 16px;
  }

  h1 {
    font-size: 28px;
  }

  h2 {
    font-size: 24px;
  }

  .logo_container {
    width: 120px;
  }

  .navbar {
    justify-content: space-between;
  }
}

/* Для больших экранов (от 768px до 992px) */
@media (min-width: 769px) and (max-width: 992px) {
  body {
    font-size: 18px;
  }

  h1 {
    font-size: 32px;
  }

  h2 {
    font-size: 28px;
  }

  .logo_container {
    width: 140px;
  }

  .navbar {
    justify-content: space-around;
  }

  .footer {
    font-size: 14px;
  }
}

/* Для очень больших экранов (от 992px и выше) */
@media (min-width: 993px) {
  body {
    font-size: 20px;
  }

  h1 {
    font-size: 36px;
  }

  h2 {
    font-size: 32px;
  }

  .logo_container {
    width: 160px;
  }

  .navbar {
    justify-content: flex-start;
  }
}
