.nav {
  padding: 25px;
  font-size: 20px;

  .logo_container {
    width: fit-content;
    margin-left: 5%;
    transition: all 0.3s;
    position: relative;
    display: inline-block;
  }

  a {
    display: inline-block;
    text-underline-offset: 5px;
  }

  .logo_link {
    color: $text-color-light;
  }

  .logo_link:hover {
    background-color: rgba(255, 255, 255, 0);
  }

  .logo_container::before {
    content: '';
    position: absolute;
    width: 100px;
    height: 10px;
    background-color: $secondary-color-light;
    border-radius: 50%;
    transform: translate(-5%, 100%) scale(0);
    transition: transform 0.5s ease;
    z-index: -1;
  }

  .logo_container:hover::before {
    transform: translate(-5%, 100%) scale(1);
  }

  .logo {
    display: block;
    position: relative;
    transition: transform 0.5s ease;
    z-index: 1;
  }

  .logo:hover {
    transform: translateY(-5px);
  }

  .nav_list_item_active {
    text-decoration: underline;
  }
}

.container {
  display: grid;
  grid-template-columns: 2fr 1fr;
  flex-direction: row;

  ul {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
}

.theme_btn {
  order: 9;

  position: relative;
  display: flex;
  justify-content: space-between;

  width: 51px;
  height: 27px;
  padding: 5px;

  border-radius: 50px;

  img {
    width: 40%;
    height: auto;
  }
}

.theme_btn::before {
  content: '';

  position: absolute;
  top: 1px;
  left: 1px;

  display: block;
  width: 24px;
  height: 24px;

  border-radius: 50%;
  background-color: #fff;

  transition: left 0.2s ease-in;
}

.theme_btn-active::before {
  left: 24px;
}

.dark_theme_btn_icon {
  position: relative;
  z-index: 9;
}

/* Адаптивная верстка */

/* Для маленьких экранов (мобильные устройства, до 576px) */
@media (max-width: 576px) {
  .nav {
    padding: 15px;
    font-size: 16px;

    .logo_container {
      margin-left: 0;
      width: 100%;
      text-align: center;
    }

    ul {
      flex-direction: column;
      align-items: center;
      padding: 0;
    }

    a {
      padding: 10px;
      display: block;
      text-align: center;
    }
  }

  .container {
    grid-template-columns: 1fr;
    ul {
      justify-content: center;
      flex-direction: column;
    }
  }

  .theme_btn {
    width: 45px;
    height: 25px;
  }

  .theme_btn::before {
    width: 22px;
    height: 22px;
  }
}

/* Для планшетов и средних экранов (от 576px до 768px) */
@media (min-width: 577px) and (max-width: 768px) {
  .nav {
    padding: 20px;
    font-size: 18px;

    ul {
      flex-direction: row;
      justify-content: space-between;
    }

    .logo_container {
      margin-left: 2%;
      width: fit-content;
    }

    a {
      padding: 8px;
    }
  }

  .container {
    grid-template-columns: 2fr 1fr;
    ul {
      justify-content: space-around;
    }
  }

  .theme_btn {
    width: 48px;
    height: 26px;
  }

  .theme_btn::before {
    width: 23px;
    height: 23px;
  }
}

/* Для больших экранов (от 768px до 992px) */
@media (min-width: 769px) and (max-width: 992px) {
  .nav {
    padding: 25px;
    font-size: 20px;

    ul {
      justify-content: space-between;
    }

    a {
      padding: 7px;
    }
  }

  .container {
    grid-template-columns: 2fr 1fr;
  }

  .theme_btn {
    width: 50px;
    height: 27px;
  }

  .theme_btn::before {
    width: 24px;
    height: 24px;
  }
}

/* Для очень больших экранов (от 992px и выше) */
@media (min-width: 993px) {
  .nav {
    padding: 25px;
    font-size: 20px;

    ul {
      justify-content: space-around;
    }

    a {
      padding: 7px;
    }
  }

  .container {
    grid-template-columns: 2fr 1fr;
  }

  .theme_btn {
    width: 51px;
    height: 27px;
  }

  .theme_btn::before {
    width: 24px;
    height: 24px;
  }
}
