.about {
  padding: 30px;
  display: grid;
  grid-template-columns: 1.7fr 1.3fr;
  .avatar img {
    width: 512px;
    height: auto;
    border-radius: 10px;
  }
  .skills {
    margin-top: 10px;
    ul {
      margin-top: 3%;
      margin-left: 7%;
      font-size: 20px;
      li span {
        font-family: "Courier New", sans-serif;
      }
      li img {
        width: 25px;
        height: auto;
      }
    }
    .skills_list,
    .skills_list li {
      list-style: disc;
    }
    .about_me_list {
      margin-bottom: 5%;
    }
    .about_me_list,
    .about_me_list li {
      list-style-type: "-";
    }
  }
}

@media (max-width: 576px) {
  .about {
    display: flex;
    flex-direction: column;
  }
}
@media (min-width: 577px) and (max-width: 768px) {
  .about {
    display: flex;
    flex-direction: column;
  }
}
